// <template>

//   <label :class="[(buttonStyle?'btn btn-':'rb rb-')+typeColor,{'active':active}]">

//     <input type="radio" autocomplete="off"

//       v-el:input

//       :checked="active"

//       :value="value"

//       :name="name"

//       :readonly="readonly"

//       :disabled="disabled"

//       @click="toggle"

//     />

//     <span class="icon"></span>

//     <span><slot></slot></span>

//   </label>

// </template>


// <script>
import coerceBoolean from './utils/coerceBoolean.js'

export default {
  props: {
    value: {
      default: true
    },
    checked: {
      twoWay: true
    },
    button: {
      type: Boolean,
      coerce: coerceBoolean,
      default: false
    },
    disabled: {
      type: Boolean,
      coerce: coerceBoolean,
      default: false
    },
    name: {
      type: String,
      default: null
    },
    readonly: {
      type: Boolean,
      coerce: coerceBoolean,
      default: false
    },
    type: {
      type: String,
      default: null
    }
  },
  computed: {
    active () {
      return this.group ? this.$parent.value === this.value : this.value === this.checked
    },
    buttonStyle () {
      return this.button || (this.group && this.$parent.buttons)
    },
    group () {
      return this.$parent && this.$parent._radioGroup
    },
    typeColor () {
      return (this.type || (this.$parent && this.$parent.type)) || 'default'
    }
  },
  created () {
    const parent = this.$parent
    if (!parent) return
    if (parent._btnGroup && !parent._checkboxGroup) {
      parent._radioGroup = true
    }
  },
  ready () {
    if (!this.$parent._radioGroup) return
    if (this.$parent.value) {
      this.checked = (this.$parent.value === this.value)
    } else if (this.checked) {
      this.$parent.value = this.value
    }
  },
  methods: {
    focus () {
      this.$els.input.focus()
    },
    toggle () {
      this.focus()
      this.checked = this.value
      if (this.group) {
        this.$parent.value = this.value
      }
    }
  }
}
// </script>


